<template>
  <div class="users">
    <h1>SelfStart</h1>
    <div class="container">
      <p class="error" v-if="error">{{ error }}</p>
      <div
        v-for="(user, index) in users"
        v-bind:key="user._id"
        v-bind:user="user"
        v-bind:index="index"
      >
        <p class="text">{{ user.email }}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import User from "../services/User";

  export default {
    name: "Users",
    data() {
      return {
        users: "",
        error: "",
      };
    },
    async created() {
      try {
        this.users = await User.getUsers();
      } catch (err) {
        this.error = err;
      }
    },
  };
</script>
